import { Link as ChakraLink, Text } from "@chakra-ui/react"
import { Link as GatsbyLink } from "gatsby"
import React from "react"
import { globalHistory } from "@reach/router"

const path = globalHistory.location.pathname

const Link = ({ children, ...props }) => {
  const isInternalLink =
    props.href &&
    !props.href.startsWith("//") &&
    !props.href.startsWith("http") &&
    !props.href.startsWith(".") &&
    (props.href.startsWith("/") || /\w+\.md(#\w+)?/.test(props.href))

  const menuPathPrefix = path.replace(/\/[^/]*(\/)?$/, "")

  const to = isInternalLink
    ? `/${menuPathPrefix}/${props.href.replace(/\.md/, "")}`.replace(
        /\/+/g,
        "/"
      )
    : props.href

  return (
    <>
      {isInternalLink ? (
        <Text as="span" color="rlBlue">
          <GatsbyLink
            to={to}
            style={{
              color: "rlBlue",
            }}
          >
            {children}
          </GatsbyLink>
        </Text>
      ) : (
        <ChakraLink color="rlBlue" href={to}>
          {children}
        </ChakraLink>
      )}
    </>
  )
}

export default Link
